<template>
  <div id="slider" class="pa-10">
    <div class="slider-content-block d-flex align-center">
      <transition :name="slideDirection" mode="out-in">
        <keep-alive>
          <component
            :class="slideDirection"
            v-bind:is="arrOfBlocks[currentBlock]"
          ></component>
        </keep-alive>
      </transition>
    </div>
    <div class="slider-delimiters-block mt-10">
      <template v-for="(item, index) in arrOfBlocks">
        <div
          :key="index"
          :class="['delimiter-btn', index === currentBlock ? 'active' : '']"
          @click="enableStatus ? changeBlock(index) : null"
        ></div>
      </template>
    </div>
  </div>
</template>

<script>
import vFirstSlide from "./vFirstSlide/index.vue";
import vSecondSlide from "./vSecondSlide/index.vue";
import vThirdSlide from "./vThirdSlide/index.vue";
import vFourthSlide from "./vFourthSlide/index.vue";
export default {
  name: "slider",
  data: function() {
    return {
      currentBlock: 0,
      arrOfBlocks: [vFirstSlide, vSecondSlide, vThirdSlide, vFourthSlide],
      slideDirection: "slide-right",
      scroolTimer: "",
      enableStatus: true
    };
  },
  components: {
    vFirstSlide,
    vSecondSlide,
    vThirdSlide,
    vFourthSlide
  },
  methods: {
    changeBlock(index) {
      this.enableStatus = false;
      if (index === this.currentBlock.element) {
        return null;
      } else if (index > this.currentBlock) {
        this.clearTimer(this.scroolTimer);
        this.slideDirection = "slide-left";
        this.currentBlock = index;
      } else {
        this.clearTimer(this.scroolTimer);
        this.slideDirection = "slide-right";
        this.currentBlock = index;
      }
      setTimeout(() => {
        //this.restartTimer();
        this.enableStatus = true;
      }, 1000);
    },
    automaticScrool() {
      this.enableStatus = false;
      this.slideDirection = "slide-left";
      if (this.currentBlock + 1 === this.arrOfBlocks.length) {
        this.currentBlock = 0;
      } else {
        this.currentBlock += 1;
      }
      setTimeout(() => {
        this.enableStatus = true;
      }, 1000);
    },
    restartTimer() {
      this.setTimer();
    },
    clearTimer() {
      clearInterval(this.scroolTimer);
    },
    setTimer() {
      this.scroolTimer = setInterval(() => {
        this.automaticScrool();
      }, 5000);
    }
  },
  mounted() {
    //this.setTimer();
  }
};
</script>

<style scoped lang="scss">
#slider {
  position: relative;
  top: 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1050px;
  color: white;
}

.slider-content-block {
  width: 100%;
  min-height: 394px;
  max-height: 394px;
}

@media screen and (max-width: 500px) {
  .slider-content-block {
    min-height: 250px;
  }
}

.slider-delimiters-block {
  display: flex;
  justify-content: center;
  width: 100%;
}

.delimiter-btn {
  width: 20px;
  height: 12px;
  margin: 0 10px;
  background: orange;
  transform: skewX(-45deg);
}

.active {
  background: white;
  border: 1px solid #00cc00;
}

.slide-left-enter-active {
  animation: slide-right 0.5s;
}
.slide-left-leave-active {
  animation: slide-left 0.5s reverse;
}

.slide-right-enter-active {
  animation: slide-left 0.5s;
}
.slide-right-leave-active {
  animation: slide-right 0.5s reverse;
}

@keyframes slide-left {
  0% {
    transform: translate(-150px, 40px);
    opacity: 0;
  }
  100% {
    transform: translate(0);
    opacity: 1;
  }
}

@keyframes slide-right {
  0% {
    transform: translate(150px, 40px);
    opacity: 0;
  }
  100% {
    transform: translate(0);
    opacity: 1;
  }
}
</style>
